import React, { useEffect, useState } from 'react';
import ReactDOM from 'react-dom';
import { connect } from 'react-redux';
import { delet } from '../actions';
import UserCard from '../components/UserCard';
import '../styles/form.css'

const Delet = ({ users, match, delet, history }) => {
    let myUser = users.find(user => user.email === match.params.email);
    let [form, setForm] = useState({
        name: myUser.name,
        email: myUser.email,
        password: ''
    });
    let [complete, setComplete] = useState(true);
    let [correct, setCorrect] = useState(true);
    let [portal, setPortal] = useState(false);
    function handleChange(e) {
        setForm({
            ...form,
            [e.target.name]: e.target.value,
        });
    }
    function handleClick() {
        if (form.email && form.password) {
            setComplete(true);
            if (form.password !== myUser.password || form.email !== myUser.email) {
                setCorrect(false);
            } else {
                setCorrect(true);
            }
        } else {
            setComplete(false);
        }
    }
    useEffect(() => {
        document.getElementById("title").innerHTML = `Delete: ${match.params.email}`;
    })
    return (
        <main className='form'>
            <UserCard name={form.name || 'Your Name'} email={myUser.email || 'Your Email'} />
            <form onSubmit={e => {
                    e.preventDefault();
                    if (complete && correct) {
                        setPortal(true);
                    }
                }}>
                <label htmlFor='email' className='normal'>your email</label>
                <input type="text" name="email" autoComplete='email' placeholder='your email' id='email' required onChange={handleChange} />
                <label htmlFor='password' className='normal'>your password</label>
                <input type="password" name="password" autoComplete='password' placeholder='your password' id='password' required onChange={handleChange} />
                <button onClick={handleClick}>delete</button>
                {!complete && <h1 className='complete' style={{color: "red"}}>complete everything!</h1>}
                {!correct && <h1 className='complete' style={{color: "red"}}>the password or the email is not correct!</h1>}
                {portal && ReactDOM.createPortal(<div className='modales'>
                    <div className='modal'>
                        <h1 style={{color: 'red'}}>Are you sure?</h1>
                        <button className='deletebutton' onClick={() => {
                            history.push('/users/home');
                            delet(form.password);
                        }}>delete</button><button className='closeButton' onClick={() => {setPortal(false)}}>X</button>
                    </div>
                </div>, document.getElementById('modal'))}
            </form>
        </main>
    );
}

const mapStateToProps = (state) => ({
    users: state.users
});

const mapDispatchToProps = {
    delet,
}

export default connect(mapStateToProps, mapDispatchToProps)(Delet);