import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom'
import Loading from '../components/Loading';
import UserCard from '../components/UserCard';
import { delet } from '../actions';

function MainPage({ users, delet }) {
    let [data, setData] = useState({
        loading: true,
    });
    let [filter, setFilter] = useState(users);
    function setPage() {
        setTimeout(() => {setData({
            loading: false,
        })}, 1500);
    }
    useEffect(() => {
        setPage();
        document.getElementById("title").innerHTML = "Users";
    }, []);
    return (
        <main style={{textAlign: 'center'}}>
            <input type='text' placeholder='search the name' style={{
                width: '50%',
                border: '3px solid black',
                padding: '5px 15px',
                borderRadius: '100px',
                color: 'purple'
            }} onChange={(e) => {
                let filtereds = users.filter(user => user.name.toLowerCase().includes(e.target.value.toLowerCase()));
                setFilter(filtereds);
            }} />
            { !data.loading ?
                filter.length > 0 ? <>{filter.map(user => <><UserCard key={user.email} {...user} edit='true' /><br /><Link to={`/users/${user.email}/delet`} className='aButton'>delete</Link></>)}</>
                :
                <><h1>users not found:<br /><Link className='aButton' to='/users/form'>create the yours!</Link></h1></> : <Loading /> }
        </main>
    )
}

const mapDispatchToProps = {
    delet,
};

const mapStateToProps = (state) => ({
    users: state.users
})

export default connect(mapStateToProps, mapDispatchToProps)(MainPage);